import React, { useState, useEffect } from "react";

const FilterForm = ({ products, setFilteredProducts }) => {
  const [inputValues, setInputValues] = useState({
    name: "",
    category: "all",
  });

  const inputHandler = (e) => {
    e.preventDefault();
    switch (e.target.type) {
      case "text":
        setInputValues({ ...inputValues, name: e.target.value });
        break;
      case "select-one":
        setInputValues({ ...inputValues, category: e.target.value });
        break;
      case "submit":
        setFilteredItems();
        break;
      default:
        break;
    }
  };

  const nameFilter = () => {
    return inputValues.name === ""
      ? products
      : products.filter((item) => item.name.includes(inputValues.name));
  };

  const categoryFilter = () => {
    return inputValues.category === "all"
      ? products
      : products.filter((item) => item.category.includes(inputValues.category));
  };

  const setFilteredItems = () => {
    setFilteredProducts(
      nameFilter().filter((item) => categoryFilter().indexOf(item) >= 0)
    );
  };

  useEffect(setFilteredItems, [products]);

  return (
    <form className="filter-form">
      <input
        onChange={inputHandler}
        value={inputValues.name}
        className="filter-input"
        type="text"
        placeholder="დასახელება"
      />

      <select
        onChange={inputHandler}
        value={inputValues.category}
        className="filter-input"
      >
        <option value="all">ყველა</option>
        <option value="meat">ხორცეული</option>
        <option value="vegetable">ბოსტნეული</option>
        <option value="fruit">ხილი</option>
        <option value="other">სხვა</option>
      </select>

      <input
        onClick={inputHandler}
        className="btn"
        type="submit"
        value="ძებნა"
      />
    </form>
  );
};

export default FilterForm;
