import React from "react";

const Product = ({
  name,
  category,
  quantity,
  weight,
  checked,
  price,
  id,
  products,
  setProducts,
}) => {
  const checkItemHandler = () => {
    const checkItem = products.find((el) => el.id === id);
    checkItem.checked = true;
    const otherItems = products.filter((item) => item !== checkItem);

    setProducts([...otherItems, checkItem]);
  };

  const deleteItemHandler = () => {
    setProducts(products.filter((item) => item.id !== id));
  };

  return (
    <li className={`product-item ${checked && "item-checked"}`}>
      <h3 className="product-name">{name}</h3>
      <div className={`category-icon icon-${category}`}></div>
      <span className="value value-red">
        {quantity}
        {weight}
      </span>
      <span className="value value-blue">{price}₾</span>
      {!checked ? (
        <button onClick={checkItemHandler} className="btn-check"></button>
      ) : (
        <button onClick={deleteItemHandler} className="btn-delete"></button>
      )}
    </li>
  );
};

export default Product;
