import React, { useState } from "react";
const shortid = require("shortid");

// Handle Events
const AddItem = ({ products, setProducts }) => {
  const [inputValues, setInputValues] = useState({
    name: "",
    category: "",
    quantity: 1,
    checked: false,
    weight: "X",
    price: 1,
  });

  const inputHandler = (e) => {
    e.preventDefault();
    switch (e.target.id) {
      case "name":
        e.target.value.length < 16 &&
          setInputValues({
            ...inputValues,
            name: e.target.value,
          });
        break;
      case "category":
        setInputValues({
          ...inputValues,
          category: e.target.value,
        });
        break;
      case "weight":
        setInputValues({
          ...inputValues,
          weight: e.target.value,
        });
        break;
      case "price":
        e.target.value.match(
          /^\d{0,2}([.]\d{0,2})?([₾])?$|^\d{0,1}([₾])(\d)?$|^\d{0,2}([₾])([.])$|^\d{0,2}([.])(\d)?([₾])\d$/
        ) &&
          setInputValues({
            ...inputValues,
            price: e.target.value.replace("₾", ""),
          });

        e.target.value === inputValues.price &&
          setInputValues({
            ...inputValues,
            price: e.target.value.slice(0, e.target.value.length - 1),
          });
        break;
      default:
        break;
    }
  };

  const operatorHandler = (e) => {
    e.preventDefault();

    switch (e.target.id) {
      case "quantity-down":
        inputValues.quantity > 1 &&
          setInputValues({
            ...inputValues,
            quantity: inputValues.quantity - 1,
          });
        break;

      case "quantity-up":
        setInputValues({
          ...inputValues,
          quantity: inputValues.quantity + 1,
        });
        break;

      case "value-down":
        inputValues.price > 1 &&
          setInputValues({
            ...inputValues,
            price: Number(inputValues.price) - 1,
          });
        break;

      case "value-up":
        inputValues.price < 100 &&
          setInputValues({
            ...inputValues,
            price: Number(inputValues.price) + 1,
          });
        break;
      default:
        break;
    }
  };

  const submitHandler = (e) => {
    e.preventDefault();
    let currentItem = inputValues;
    currentItem.id = shortid.generate();
    currentItem.category === "" && (currentItem.category = "other");
    currentItem.price === "" && (currentItem.price = 1);
    setProducts([currentItem, ...products]);

    setInputValues({
      name: "",
      category: "",
      quantity: 1,
      checked: false,
      weight: "X",
      price: 1,
    });
  };

  return (
    <form onSubmit={submitHandler} className="add-form">
      <input
        onChange={inputHandler}
        value={inputValues.name}
        id="name"
        className="add-input"
        type="text"
        placeholder="შეიყვანეთ სასურველი პროდუქტი..."
        required
      />

      <select
        onChange={inputHandler}
        value={inputValues.category}
        id="category"
        className="add-input"
      >
        <option value="" disabled hidden>
          აირჩიეთ პროდუქტის სახეობა
        </option>
        <option value="meat">ხორცეული</option>
        <option value="vegetable">ბოსტნეული</option>
        <option value="fruit">ხილი</option>
        <option value="other">სხვა</option>
      </select>

      <div className="values-container">
        {/* Price Component */}
        <div className="add-input">
          <button
            onClick={operatorHandler}
            id="value-down"
            className="btn btn-minus"
          ></button>
          <input
            type="text"
            onChange={inputHandler}
            id="price"
            className="value"
            value={inputValues.price + "₾"}
          />
          <button
            onClick={operatorHandler}
            id="value-up"
            className="btn btn-plus"
          ></button>
        </div>

        {/* Quantity Component */}
        <div className="add-input">
          <button
            onClick={operatorHandler}
            id="quantity-down"
            className="btn btn-minus"
          ></button>
          <select
            onChange={inputHandler}
            value={inputValues.weight}
            id="weight"
            className="value"
          >
            <option value="X">{inputValues.quantity}X</option>
            <option value="KG">{inputValues.quantity}KG</option>
          </select>
          <button
            onClick={operatorHandler}
            id="quantity-up"
            className="btn btn-plus"
          ></button>
        </div>
      </div>

      <input className="btn btn-add" type="submit" value="დამატება" />
    </form>
  );
};

export default AddItem;
