import React, { useState } from "react";
import "./App.css";
import FilterForm from "./components/FilterForm";
import AddItem from "./components/AddItem";
import ShowItems from "./components/ShowItems";

function App() {
  const [products, setProducts] = useState([]);
  const [filteredProducts, setFilteredProducts] = useState([]);

  const contChangeHandler = () => {
    const leftCont = document.querySelector(".container-wrapper-left");
    const rightCont = document.querySelector(".container-wrapper-right");
    leftCont.classList.toggle("transition-out");
    rightCont.classList.toggle("transition-in");
  };

  return (
    <div className="App">
      <div className="container">
        <div className="container-wrapper-left">
          <h1 className="logo">პროდუქტების კალათა</h1>
          <AddItem products={products} setProducts={setProducts} />
          <button onClick={contChangeHandler} className="btn btn-transition">
            პროდუქტების ნახვა
          </button>
        </div>
        <div className="container-wrapper-right">
          <FilterForm
            products={products}
            setFilteredProducts={setFilteredProducts}
          />
          <ShowItems
            products={products}
            setProducts={setProducts}
            filteredProducts={filteredProducts}
          />
          <button onClick={contChangeHandler} className="btn btn-transition">
            პროდუქტის დამატება
          </button>
        </div>
      </div>
      <div className="circle circle-left"></div>
      <div className="circle circle-right"></div>
    </div>
  );
}

export default App;
