import React from "react";
import Product from "./Product";

const ShowItems = ({ products, setProducts, filteredProducts }) => {
  let moneyCounter = 0;
  filteredProducts.length > 0 &&
    (moneyCounter = filteredProducts
      .reduce((total, item) => total + Number(item.price), 0)
      .toFixed(2));

  switch (filteredProducts.length) {
    case 0:
      let emptyBasket = "";
      products.length === 0
        ? (emptyBasket = "კალათა ცარიელია")
        : (emptyBasket = "პროდუქტი არ მოიძებნა");

      return (
        <div className="empty-basket">
          {emptyBasket} <i className="fas fa-shopping-basket"></i>
        </div>
      );

    default:
      return (
        <div className="showitems">
          <ul className="product-list">
            {filteredProducts.map((item) => {
              return (
                <Product
                  name={item.name}
                  category={item.category}
                  quantity={item.quantity}
                  weight={item.weight}
                  checked={item.checked}
                  id={item.id}
                  price={item.price}
                  products={products}
                  setProducts={setProducts}
                  key={item.id}
                />
              );
            })}
          </ul>
          <div className="product-features">
            <div className="value-sum">
              ჯამი: <span>{moneyCounter}₾</span>
            </div>
            <button onClick={() => setProducts([])} className="btn">
              გასუფთავება
            </button>
          </div>
        </div>
      );
  }
};

export default ShowItems;
